"use client";

import { useState } from 'react';
import Header from '@/components/Header';
import Footer from '@/components/Footer';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

export default function RegistrationPage() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    institution: '',
    city: '',
    scfhs: ''
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log('Form submitted:', formData);
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-600 via-blue-500 to-blue-400 relative overflow-hidden">
      {/* Background geometric shapes */}
      <div className="absolute inset-0">
        <div className="absolute top-0 left-0 w-96 h-96 bg-green-400 rounded-full opacity-20 -translate-x-1/2 -translate-y-1/2"></div>
        <div className="absolute bottom-0 right-0 w-80 h-80 bg-yellow-400 rounded-full opacity-20 translate-x-1/2 translate-y-1/2"></div>
        <div className="absolute top-1/2 right-0 w-64 h-64 bg-orange-400 opacity-20 translate-x-1/2 -translate-y-1/2"></div>
        <div className="absolute bottom-0 left-1/4 w-72 h-72 bg-green-500 opacity-20 translate-y-1/2"></div>
      </div>

      <Header />

      <main className="relative z-10">
        {/* Hero Section */}
        <section className="container mx-auto px-4 py-16 text-center text-white">
          <h1 className="text-4xl md:text-5xl font-bold mb-4">
            Saudi Rheumatology Conference
          </h1>
          <h2 className="text-3xl md:text-4xl font-bold mb-2">
            Registration <span className="text-yellow-300">2025</span>
          </h2>
        </section>

        {/* Registration Form Section */}
        <section className="bg-gray-100 py-16">
          <div className="container mx-auto px-4">
            <div className="max-w-2xl mx-auto">
              <Card className="shadow-xl">
                <CardHeader className="text-center bg-gray-50">
                  <CardTitle className="text-2xl font-bold text-gray-800 mb-2">
                    Registration Form
                  </CardTitle>
                  <p className="text-gray-600 mb-2">
                    WELCOME TO THEThe Impact of healthcare reform on Rheumatology practice in Saudi Arabia 2025
                  </p>
                  <p className="text-sm text-gray-500">
                    INTERNATIONAL CONFERENCE REGISTRATION PAGE
                  </p>
                </CardHeader>
                
                <CardContent className="p-8">
                  <form onSubmit={handleSubmit} className="space-y-6">
                    {/* Name Field */}
                    <div className="space-y-2">
                      <Label htmlFor="name" className="text-sm font-medium text-gray-700">
                        Name
                      </Label>
                      <Select onValueChange={(value) => handleInputChange('namePrefix', value)}>
                        <SelectTrigger className="w-full">
                          <SelectValue placeholder="Prof" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="prof">Prof</SelectItem>
                          <SelectItem value="dr">Dr</SelectItem>
                          <SelectItem value="mr">Mr</SelectItem>
                          <SelectItem value="ms">Ms</SelectItem>
                          <SelectItem value="mrs">Mrs</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    {/* Email Field */}
                    <div className="space-y-2">
                      <Label htmlFor="email" className="text-sm font-medium text-gray-700">
                        Email
                      </Label>
                      <Input
                        id="email"
                        type="email"
                        value={formData.email}
                        onChange={(e) => handleInputChange('email', e.target.value)}
                        className="w-full"
                        required
                      />
                    </div>

                    {/* Phone Field */}
                    <div className="space-y-2">
                      <Label htmlFor="phone" className="text-sm font-medium text-gray-700">
                        Phone
                      </Label>
                      <Input
                        id="phone"
                        type="tel"
                        value={formData.phone}
                        onChange={(e) => handleInputChange('phone', e.target.value)}
                        className="w-full"
                        required
                      />
                    </div>

                    {/* Institution Field */}
                    <div className="space-y-2">
                      <Label htmlFor="institution" className="text-sm font-medium text-gray-700">
                        Institution/
                        <br />
                        Place of work
                      </Label>
                      <Input
                        id="institution"
                        type="text"
                        value={formData.institution}
                        onChange={(e) => handleInputChange('institution', e.target.value)}
                        className="w-full"
                        required
                      />
                    </div>

                    {/* City Field */}
                    <div className="space-y-2">
                      <Label htmlFor="city" className="text-sm font-medium text-gray-700">
                        City
                      </Label>
                      <Input
                        id="city"
                        type="text"
                        value={formData.city}
                        onChange={(e) => handleInputChange('city', e.target.value)}
                        className="w-full"
                        required
                      />
                    </div>

                    {/* SCFHS Field */}
                    <div className="space-y-2">
                      <Label htmlFor="scfhs" className="text-sm font-medium text-gray-700">
                        SCFHS no.
                      </Label>
                      <Input
                        id="scfhs"
                        type="text"
                        value={formData.scfhs}
                        onChange={(e) => handleInputChange('scfhs', e.target.value)}
                        className="w-full"
                      />
                    </div>

                    {/* Submit Button */}
                    <div className="pt-6">
                      <Button 
                        type="submit"
                        className="w-full bg-blue-600 hover:bg-blue-700 text-white py-3 text-lg font-semibold"
                      >
                        Book →
                      </Button>
                    </div>
                  </form>
                </CardContent>
              </Card>
            </div>
          </div>
        </section>
      </main>

      <Footer />
    </div>
  );
}
